.navbar{
    width: 100%;
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    padding: 0.4rem 0;
}

.navbar span{
    flex: 0.5;
}

.navbar img{
    cursor: pointer;
}